.App{
  width: 100%;
  height: 100%;
  
  // &::after{
  //   content: "";
  //   height: 100%;
  //   display: block;
  //   clear: both;
  // }
  .center-header{
    width:1170px;
    margin: 0 auto;
  }
  .page-header{
    width: 100%;
    float: left;
    margin: 0 auto;
    background: #ff6a00;
    box-shadow: 0 1px 3px #303030 ;
    .header-nav{
      width: 570px;
      float: right;
      padding: 0;
      margin: 0;
      li{
        padding: 0px 10px 0px 10px;
        float: left;
        list-style:none;
        margin-right: 16px;
        transition: all 0.3s ease-in;
        border-top: 3px solid #fd6b20;
        &:hover{
          border-top: 3px solid #ffffff;
        }
        >a{
          padding: 21px 0 21px 0 ;
          display: block;
          line-height: 55px;
          color: #ffffff;
          text-align: center;
          font-size: 18px;
          font-weight: 600;
          
        }
      }
      .active{
        border-top: 3px solid #ffffff;
      }
    }
    &::after{
      content: "";
      height: 100%;
      display: block;
      clear: both;
    }
    .logo{
      margin-top: 23px;
      width: 217px;
      float: left;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
}
@media (prefers-reduced-motion: no-preference) {
  .animation {
    animation: translateYs 1s ;
  }
}
@keyframes translateYs {
  0% {
      opacity: 0;
      transform: translateY(-20px);
  }
  100% {
      opacity: 1;
      transform: translateY(0);
  }
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
